.detect-container {
    text-align: center;
    padding: 40px;
  }
  
  .detect-title {
    font-size: 2rem;
    color: #0077cc;
    margin-bottom: 20px;
  }
  
  .detect-upload {
    border: 2px dashed #0077cc;
    padding: 20px;
    border-radius: 8px;
    display: inline-block;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .detect-upload:hover {
    transform: scale(1.1);
    border-color: #005bb5;
  }
  
  .detect-preview {
    margin-top: 20px;
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  