.home-container {
    text-align: center;
    padding: 40px;
    animation: fadeInUp 1.5s ease-in-out;
  }
  
  .home-title {
    font-size: 2.5rem;
    color: #0077cc;
    margin-bottom: 20px;
  }
  
  .home-description {
    font-size: 1.2rem;
    color: #444;
    margin-bottom: 30px;
    line-height: 1.5;
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  