.about-container {
    padding: 20px;
    max-width: 800px;
    margin: 20px auto;
    animation: slideIn 1s ease-out;
  }
  
  .about-title {
    font-size: 2rem;
    color: #0077cc;
    margin-bottom: 20px;
  }
  
  .about-text {
    font-size: 1rem;
    color: #555;
    line-height: 1.8;
  }
  
  @keyframes slideIn {
    from {
      transform: translateX(-100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
  