.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0077cc;
    padding: 10px 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .navbar-logo {
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
    text-decoration: none;
  }
  
  .navbar-links {
    display: flex;
    gap: 20px;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.3s ease-in-out;
  }
  
  .navbar-links a:hover {
    color: #ffcc00;
  }
  